import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import { ISettingSwitchProps, SettingSwitch } from "./settingSwitch";

interface ISettingsGroupProps {
    settings: ISettingSwitchProps[];
}

interface ISettingsGroupState {
    settings: ISettingSwitchProps[];
}

export class SettingsGroup extends React.Component<ISettingsGroupProps, ISettingsGroupState> {
    constructor(props: ISettingsGroupProps) {
        super(props);

        this.state = {
            ...props,
        };
    }

    shouldComponentUpdate(nextProps: Readonly<ISettingsGroupProps>, nextState: Readonly<ISettingsGroupState>, nextContext: any): boolean {
        if (nextProps.settings !== this.props.settings) {
            this.setState({
                ...nextProps
            });
        }

        return true;
    }

    render() {
        return (
            <div id="settings_container">
                <form action =''>
                    <fieldset>
                        {this.state.settings.map(setting => {
                            return <SettingSwitch key={setting.type} type={setting.type} enabled={setting.enabled} />
                        })}
                    </fieldset>
                </form>
            </div>
        );
    }
}