import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import { SettingsGroup } from "./settingsGroup";
import { Whitelist } from "./whitelitst";
import { ErrorBoundary } from "../components/errorBoundary";
import Header from "../components/header";


interface ISettingsProps {

}

interface ISettingsState {
    threatFilter: boolean;
    linkScanner: boolean;
    whitelist: Array<string>;
    showErrorMessage: boolean;
}

export class SettingsPage extends React.Component<ISettingsProps, ISettingsState> {
    constructor(props: ISettingsProps) {
        super(props);

        this.state = {
            threatFilter: true,
            linkScanner: true,
            whitelist: [],
            showErrorMessage: false,
        } as ISettingsState;

        this.initialize();
    }

    async initialize() {
        // This is used to display the settings page from the extension options link (for chrome) and preferences tab (for firefox).
        let tf = await BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.GET_SETTING,
            type: BDTLL.SettingType.THREAT_FILTER
        });
        let ls = await BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.GET_SETTING,
            type: BDTLL.SettingType.SEARCH_ANALYZER
        });
        let whitelist = await BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.GET_WHITELIST
        });
        let lastUpdate = await BDTLL.MessageService.messageBackground({
            command: BDTLL.Command.GET_LAST_UPDATE
        });

        this.setState({
            threatFilter: tf,
            linkScanner: ls,
            whitelist: whitelist,
        });
    }

    render() {
        return (
            <div className="animate-in">
                <div className="switch_container">
                    <SettingsGroup
                        settings={[
                            {
                                type: BDTLL.SettingType.THREAT_FILTER,
                                enabled: this.state.threatFilter
                            },
                            {
                                type: BDTLL.SettingType.SEARCH_ANALYZER,
                                enabled: this.state.linkScanner
                            },
                            {
                                type: BDTLL.SettingType.EXCEPTION_LIST,
                                enabled: true
                            }
                        ]}
                    />

                    <Whitelist items={this.state.whitelist}/>
                </div>
            </div>
        )
    }
};

window.addEventListener('DOMContentLoaded', async () => {
    const windowTitleElement = document.getElementById("window_title");
    if (windowTitleElement) {
        ReactDOM.render(
            BDTLL.Utils.get_localized_text("extension_name") as any,
            windowTitleElement
        );
    }

    const settingsPageViewElement = document.getElementById("settingsPageView");
    if (settingsPageViewElement) {
        ReactDOM.render(
            <div>
                <ErrorBoundary>
                    <Header/>
                    <SettingsPage />
                </ErrorBoundary>
            </div>,
            settingsPageViewElement
        );

        document.body.classList.add("animate-in");
    }
});
